import React from 'react'

const Item = ({ dataItem, onGetCoinDetails, coinNumber ,  isShowCoinDetails }) => {
    let run = () =>{
        let selectCoinID = dataItem.CoinInfo.Id;
        isShowCoinDetails();
        debugger
        onGetCoinDetails(selectCoinID);
    }
    return (
        <li onClick={ ()=>run()}>
            {coinNumber}
            {dataItem.CoinInfo.Id}
            {dataItem.CoinInfo.Name}
            <img src={`https://www.cryptocompare.com${dataItem.CoinInfo.ImageUrl}`} />
        </li>
    )
}


export default Item